import React, { Component } from "react";
import "./App.css";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./components/home";
import Item from "./components/item";
import Search from "./components/search";
import FlexboxFroggy from "./components/flexboxFroggy";
import NewArrival from "./components/newArrival";
import Card from "./components/card";
import CubicBezier from "./components/cubicBezier";

class App extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route exact={true} path="/" component={Home} />
          <Route path="/item" component={Item} />
          <Route path="/search" component={Search} />
          <Route path="/game" component={FlexboxFroggy} />
          <Route path="/new" component={NewArrival} />
          <Route path="/card" component={Card} />
          <Route path="/cubic" component={CubicBezier} />
        </Switch>
      </Router>
    );
  }
}

export default App;
