import React, { Component } from "react";
import "./itemInfo.css";

class ItemInfo extends Component {
  constructor(props) {
    super(props);
    this.state = { info: [] };
  }
  componentDidMount() {
    fetch("http://localhost:3000/data/data.json")
      .then(res => res.json())
      .then(data => {
        this.setState({ info: data }, function() {
          //console.log(this.state.info.item);
          console.log(this.state.info.props.基本信息);
          let module = this.state.info.props.基本信息.map((item, index) => {
            return (
              <li
                key={index}
                className="mui-standardItemProps-props j-standardItemProps-props"
              >
                <span className="">{Object.keys(item)}</span>
                <span className="">{Object.values(item)}</span>
              </li>
            );
          });
          this.setState({ detail: module }, function() {});
        });
      })
      .catch(e => console.log("错误:", e));
  }
  state = {};
  render() {
    return (
      <div className="module-container item_info">
        <h3 className="module-title">商品信息</h3>
        <div className="module-content">
          <div className="mui-standardItemProps mdv-standardItemProps">
            <div className="mui-standardItemProps-standardItemProps">
              <ul>
                {this.state.detail}
                <li className="mui-standardItemProps-props j-standardItemProps-props">
                  <span className="">产品剂型</span>
                  <span className="">外用液体剂</span>
                </li>
              </ul>
            </div>
            <div className="mui-standardItemProps-morewraper j-standardItemProps-moreattr mui-standardItemProps-arrow-up">
              <a className="mui-standardItemProps-more" href="www.baidu.com">
                收起
                <i />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ItemInfo;
