import React, { Component } from "react";
import "./bottomMenu.css";

const menus = [
  {
    img:
      "//img.alicdn.com/tfs/TB1xXfIuxYaK1RjSZFnXXa80pXa-68-68.png_50x50Q60.jpg_.webp",
    title: "分类"
  },
  {
    img:
      "//img.alicdn.com/tfs/TB1aJrzuAPoK1RjSZKbXXX1IXXa-68-68.png_50x50Q60.jpg_.webp",
    title: "药师"
  }
];
const menuRight = [
  {
    img:
      "//img.alicdn.com/tfs/TB1TsfyuwHqK1RjSZFgXXa7JXXa-68-68.png_50x50Q60.jpg_.webp",
    title: "会员有礼"
  },
  {
    img:
      "//img.alicdn.com/tfs/TB1mYHyuCzqK1RjSZFpXXakSXXa-68-68.png_50x50Q60.jpg_.webp",
    title: "购物车"
  }
];
let imgUrl = "//img.alicdn.com/tfs/TB1ETTuuxTpK1RjSZFGXXcHqFXa-110-110.gif";
const divStyle = {
  backgroundImage: "url(" + imgUrl + ")"
};

class BottomMenu extends Component {
  state = {};
  render() {
    return (
      <div
        className="bottom-menu"
        style={{ backgroundColor: "#ffffff", height: 50 }}
      >
        <div className="bottom-menu-wrapper">
          {menus.map((menu, index) => {
            return (
              <a
                key={index}
                className="item"
                style={{
                  color: "#666666",
                  borderColor: "#ffffff",
                  flexDirection: "column"
                }}
                href="#/"
              >
                <img className="icon" src={menu.img} alt="" />
                <span className="title">{menu.title}</span>
              </a>
            );
          })}
          <a
            href="#/"
            className="item"
            style={{
              color: "#666666",
              borderColor: "#ffffff",
              flexDirection: "column",
              paddingBottom: 20
            }}
          >
            <div className="is-main" style={divStyle} />
            <div
              className="main-icon-contianer"
              style={{ backgroundColor: "#ffffff", borderColor: "#ffffff" }}
            />
          </a>
          {menuRight.map((menu, index) => {
            return (
              <a
                key={index}
                className="item"
                style={{
                  color: "#666666",
                  borderColor: "#ffffff",
                  flexDirection: "column"
                }}
                href="#/"
              >
                <img className="icon" src={menu.img} alt="" />
                <span className="title">{menu.title}</span>
              </a>
            );
          })}
        </div>
      </div>
    );
  }
}

export default BottomMenu;
