import React, { Component } from "react";
import Rx from "rxjs/Rx";
import { connect } from "react-redux";
import { fromEvent } from "rxjs";
import { Observable } from "rxjs";

class Draggable extends Component {
  constructor({ x, y, zoom, changeCoord }) {
    super({ x, y, zoom, changeCoord });

    this.state = {
      dragging: false
    };
    this.size = 30;
  }
  componentDidMount() {
    const mouseEventToCoordinate = mouseEvent => ({
      x: mouseEvent.clientX,
      y: mouseEvent.clientY
    });
    const touchEventToCoordinate = touchEvent => {
      touchEvent.preventDefault();
      return {
        x: touchEvent.touches[0].clientX,
        y: touchEvent.touches[0].clientY
      };
    };

    let mouseDowns = Observable.fromEvent(this.draggable, "mousedown").map(
      mouseEventToCoordinate
    );
    let mouseMoves = Observable.fromEvent(window, "mousemove").map(
      mouseEventToCoordinate
    );
    let mouseUps = Observable.fromEvent(window, "mouseup");

    let touchStarts = Observable.fromEvent(this.draggable, "touchstart").map(
      touchEventToCoordinate
    );
    let touchMoves = Observable.fromEvent(this.draggable, "touchmove").map(
      touchEventToCoordinate
    );
    let touchEnds = Observable.fromEvent(window, "touchend");

    let dragStarts = mouseDowns.merge(touchStarts);
    let moves = mouseMoves.merge(touchMoves);
    let dragEnds = mouseUps.merge(touchEnds);

    let drags = dragStarts.concatMap(dragStartEvent => {
      const xDelta = this.props.x - dragStartEvent.x * this.props.zoom;
      const yDelta = this.props.y - dragStartEvent.y * this.props.zoom;
      return moves.takeUntil(dragEnds).map(dragEvent => {
        const x = dragEvent.x * this.props.zoom + xDelta;
        const y = dragEvent.y * this.props.zoom + yDelta;
        return { x, y };
      });
    });

    dragStarts.forEach(() => {
      this.setState({ dragging: true });
    });

    drags.forEach(coordinate => {
      this.props.changeCoord(coordinate.x, coordinate.y);
    });

    dragEnds.forEach(() => {
      this.setState({ dragging: false });
    });
  }
  state = {};
  render() {
    return (
      <g
        className={this.state.dragging ? "dragging" : "draggable"}
        ref={draggable => {
          this.draggable = draggable;
        }}
        transform={`translate(${this.props.x},${this.props.y})`}
      >
        <circle x={0} y={0} r={this.size / 2} />
        <text
          x={this.size / 2}
          y={-this.size / 2}
          textAnchor="left"
          stroke="none"
        >
          {`${this.props.x}, ${this.props.y}`}
        </text>
      </g>
    );
  }
}

const draggableMapStateToProps = (state, ownProps) => {
  return {
    zoom: state.zoom
  };
};

export default connect(draggableMapStateToProps)(Draggable);
