import React, { Component } from "react";
import Canvas from "./canvas";
import Draggable from "./draggable";
import { connect } from "react-redux";
import "./cubicBezier.css";

class UnconnectedComponent extends Component {
  state = {};
  render() {
    return (
      <div id="container">
        <Canvas>
          <line
            className="helper"
            x1={this.props.sx}
            y1={this.props.sy}
            x2={this.props.c1x}
            y2={this.props.c1y}
          />
          <line
            className="helper"
            x1={this.props.c2x}
            y1={this.props.c2y}
            x2={this.props.ex}
            y2={this.props.ey}
          />
          <path
            id="curve"
            d={`
              M ${this.props.sx} ${this.props.sy} 
              C ${this.props.c1x} ${this.props.c1y}, 
                ${this.props.c2x} ${this.props.c2y}, 
                ${this.props.ex} ${this.props.ey}`}
          />
          <Draggable
            x={this.props.sx}
            y={this.props.sy}
            changeCoord={this.props.setStartPoint}
          />
          <Draggable
            x={this.props.c1x}
            y={this.props.c1y}
            changeCoord={this.props.setControlPoint1}
          />
          <Draggable
            x={this.props.c2x}
            y={this.props.c2y}
            changeCoord={this.props.setControlPoint2}
          />
          <Draggable
            x={this.props.ex}
            y={this.props.ey}
            changeCoord={this.props.setEndPoint}
          />
        </Canvas>
        <div className="code">
          <pre>
            <p>&lt;svg width='400' height='400'&gt;</p>
            <p className="ident1">
              &lt;path d='M {this.props.sx} {this.props.sy} C {this.props.c1x}{" "}
              {this.props.c1y}, {this.props.c2x} {this.props.c2y},{" "}
              {this.props.ex} {this.props.ey}'
            </p>
            <p className="ident2">
              stroke='white' stroke-width='20' fill='transparent'/&gt;
            </p>
            <p>&lt;/svg&gt;</p>
          </pre>
        </div>
      </div>
    );
  }
}

const componentMapStateToProps = (state, ownProps) => {
  return {
    sx: state.sx,
    sy: state.sy,
    c1x: state.c1x,
    c1y: state.c1y,
    c2x: state.c2x,
    c2y: state.c2y,
    ex: state.ex,
    ey: state.ey
  };
};

const componentMapDispatchToProps = (dispatch, ownProps) => {
  return {
    setStartPoint: (x, y) => {
      dispatch({ type: "S", x, y });
    },
    setControlPoint1: (x, y) => {
      dispatch({ type: "C1", x, y });
    },
    setControlPoint2: (x, y) => {
      dispatch({ type: "C2", x, y });
    },
    setEndPoint: (x, y) => {
      dispatch({ type: "E", x, y });
    }
  };
};

export default connect(
  componentMapStateToProps,
  componentMapDispatchToProps
)(UnconnectedComponent);
