import React, { Component } from "react";
import Swiper from "swiper/dist/js/swiper.js";
import "swiper/dist/css/swiper.min.css";

let test = [
  "//img.alicdn.com/imgextra/i1/2928278102/O1CN01evM8Qt29ilS7SiuAk_!!0-item_pic.jpg",
  "//img.alicdn.com/imgextra/i3/2928278102/O1CN01jXOMCK29ilRy6lKu5_!!2928278102.jpg"
];

class ItemSlider extends Component {
  constructor(props) {
    super(props);
    this.state = { info: [] };
  }
  componentDidMount() {
    new Swiper(".swiper-container", {
      loop: true,
      autoplay: {
        disableOnInteraction: false
      },
      pagination: {
        el: ".swiper-pagination"
      }
    });
    fetch("http://localhost:3000/data/data.json")
      .then(res => res.json())
      .then(data => {
        //console.log(data);
        this.setState({ info: data }, function() {
          console.log(this.state.info.item);
          // let images = this.state.info.item.images.map((item, index) => {
          //   //console.log(item);
          // });
          //this.setState({ temp: images }, function() {});
        });
      })
      .catch(e => console.log("错误:", e));
  }

  state = {};
  render() {
    return (
      <div className="module-preview">
        <section className="mui-slider preview-slider swiper-container">
          <div className="scroller preview-scroller swiper-wrapper">
            {/* {console.log(this.state.info.item)} */}
            {/* {this.state.info.item.images.map((item, index) => {
              console.log(item);
            })} */}
            {test.map(function(item, index) {
              return (
                <a key={index} className="item swiper-slide" href="#/item">
                  <img alt="" src={item} />
                </a>
              );
            })}
            {/* {this.state.array} */}
            {/* {Object.keys(data).map(key => {
              const temp = data[key];
              return <h1>{}</h1>;
            })} */}
          </div>
          <div className="swiper-pagination" />
          <div className="nav">
            <i className="current">
              <span className="pagination">1/5</span>
            </i>
          </div>
        </section>
      </div>
    );
  }
}

export default ItemSlider;
