import React, { Component } from "react";
import "./goods.css";

const tabName = ["感冒发烧", "胃炎胃痛", "咳嗽哮喘", "便秘", "痔疮", "上火"];
const tabGood = [
  {
    img:
      "//img.alicdn.com/bao/upload/TB16WS9cfWG3KVjSZPcXXbkbXXa.jpg_300x300Q50s50.jpg_.webp",
    title: "3盒】 999三九感冒灵颗粒冲剂咽痛头痛流鼻涕鼻塞药店感冒灵常备",
    priceLeft: "¥",
    priceRight: "38",
    sold: "月销25544件"
  }
];

class Goods extends Component {
  state = {};
  render() {
    return (
      <div
        className="cate cate1"
        style={{ marginBottom: 75, marginLeft: 10, marginRight: 10 }}
      >
        <div className="title">
          <img
            className="banner"
            alt=""
            src="//img.alicdn.com/tfs/TB1ONOSyhTpK1RjSZFGXXcHqFXa-710-108.png_800x800q75.jpg_.webp"
          />
          <p className="title-name" />
        </div>
        <div className="cate-tabs-wrapper">
          <div className="cate-tabs">
            {tabName.map((data, index) => {
              return (
                <div className="cate-tab" key={index}>
                  <p className="tab">{data}</p>
                </div>
              );
            })}
          </div>
        </div>
        <div className="clearfix cate-items-content cate-items-content-1">
          {tabGood.map((item, index) => {
            return (
              <div className="item-conte-bg" key={index}>
                <a className="item-conte" href="#/">
                  <div className="img-bg">
                    <img className="img" alt="" src={item.img} />
                  </div>
                  <p className="title-con">{item.title}</p>
                  <div className="price-con">
                    <span className="price-left">{item.priceLeft}</span>
                    <span className="price-right">{item.priceRight}</span>
                  </div>
                  <p className="sold-con">{item.sold}</p>
                  <div className="j_AddCart">
                    <img
                      className="cart-con"
                      alt=""
                      src="https://gw.alicdn.com/tfs/TB1euL5qf9TBuNjy0FcXXbeiFXa-72-68.png_100x100q50s50.jpg_.webp"
                    />
                  </div>
                </a>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default Goods;
