import React, { Component } from "react";
import style from "./newArrival.module.css";
import Zmage from "react-zmage";

const itemRows = [
  {
    items: [
      {
        img:
          "//img.alicdn.com/imgextra/i3/2928278102/O1CN01od9EHq29ilPKoNCVg_!!2928278102.png_200x200.jpg_.webp",
        tip: "重金属合格",
        title: "养生堂维生素E",
        price: "148"
      },
      {
        img:
          "//img.alicdn.com/imgextra/i2/2928278102/O1CN01vdTS9n29ilRN0Sa2h_!!2928278102.png_200x200.jpg_.webp",
        tip: "重金属合格",
        title: "汤臣倍健蛋白粉",
        price: "308"
      }
    ]
  }
];

class NewArrival extends Component {
  state = {};
  render() {
    return (
      <div className={style.module}>
        <div className={style.box}>
          <div className={style.content}>
            <div className={style.title}>
              <span>新品</span>
              <span>可销售</span>
            </div>
            <div className={style.body}>
              <a className={style.banner} href="#/new">
                <img
                  src="//img.alicdn.com/tps/i4/TB1Pj0TRkzoK1RjSZFlwu1i4VXa.png"
                  alt=""
                />
              </a>
              <div className={style.item_wrapper}>
                {itemRows.map((itemRow, index) => {
                  var itemContent = itemRow.items.map((item, index) => {
                    return (
                      <a className={style.item} key={index} href="#/new">
                        <div className={style.img_wrapper}>
                          <div className={style.img_container}>
                            <Zmage src={item.img} alt="" />
                          </div>
                          <div className={style.tip}>{item.tip}</div>
                        </div>
                        <div className={style.title}>{item.title}</div>
                        <div className={style.price_wrapper}>
                          <sapn className={style.price_maker}>￥</sapn>
                          <sapn className={style.price}>{item.price}</sapn>
                          <span className={style.src_price}>258</span>
                        </div>
                      </a>
                    );
                  });
                  return (
                    <div className={style.item_row} key={index}>
                      {itemContent}
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default NewArrival;
