import React, { Component } from "react";
import Swiper from "swiper/dist/js/swiper.js";
import "swiper/dist/css/swiper.min.css";
import "./banner.css";

class Banner extends Component {
  state = {};

  componentDidMount() {
    new Swiper(".swiper-container", {
      loop: true,
      autoplay: {
        disableOnInteraction: false
      },
      pagination: {
        el: ".swiper-pagination"
      }
    });
  }
  render() {
    return (
      <div
        className="swiper-container banner-all h2-swiper slider-wrapper"
        style={{ zIndex: 0 }}
      >
        <div className="swiper-wrapper scroller">
          <a className="swiper-slide item-wrapper item" href="#/item">
            <img
              alt=""
              className="img-ks-lazyload h2-lazyload"
              src="http://img.alicdn.com/imgextra/i2/182/O1CN01N7z5Qp1DDOcHDDn4f_!!182-0-lubanu.jpg"
            />
          </a>
          <a className="swiper-slide item-wrapper item" href="#/item">
            <img
              alt=""
              className="img-ks-lazyload h2-lazyload"
              src="http://img.alicdn.com/imgextra/i1/2928278102/O1CN015Z9wQY29ilSRxGd6X_!!2928278102.jpg"
            />
          </a>
          <a className="swiper-slide item-wrapper item" href="#/item">
            <img
              alt=""
              className="img-ks-lazyload h2-lazyload"
              src="http://img.alicdn.com/imgextra/i4/152/O1CN01iMMXl51Czeje3CLNe_!!152-0-lubanu.jpg"
            />
          </a>
        </div>
        <div className="swiper-pagination" />
      </div>
    );
  }
}

export default Banner;
