class Category extends Component {
  state = {};
  render() {
    return (
      <div className="cate-wrapper">
        <div className="cate-all h2-swiper slider-wrapper">
          <div className="scroller">
            {cateItem.map((item, index) => {
              var cate = item.itemCol.map((cate, index) => {
                return (
                  <a key={index} className="cate-item" href="#/">
                    <img src={cate.img} alt="" />
                    <p>{cate.title}</p>
                  </a>
                );
              });
              return (
                <div className="item-col" key={index}>
                  {cate}
                </div>
              );
            })}
          </div>
          <div className="pagination" style={{ display: "block" }}>
            <div className="inner" style={{ left: "0%" }} />
          </div>
        </div>
      </div>
    );
  }
}

export default Category;
