import React, { Component } from "react";
import "./card.css";

const cardInfos = [
  {
    img: "//qidian.gtimg.com/acts/2019/57928942/images/boy/1_1.gif",
    tags: ["幻想世界", "长篇追读"],
    text: { h6: "我家妹妹是反派", p: "兄妹如何在死亡游戏里浪到飞起！" }
  },
  {
    img: "//qidian.gtimg.com/acts/2019/57928942/images/boy/2.png",
    tags: ["幻想世界", "长篇追读"],
    text: {
      h6: "笼中对追凶游戏",
      p: "这场游戏里，你必须找到凶手，或者成为凶手……"
    }
  },
  {
    img: "//qidian.gtimg.com/acts/2019/57928942/images/boy/2.png",
    tags: ["幻想世界", "长篇追读"],
    text: { h6: "话不可以乱说", p: "小心，这个群里说的任何话都会成真哦..." }
  },
  {
    img: "//qidian.gtimg.com/acts/2019/57928942/images/boy/2.png",
    tags: ["幻想世界", "长篇追读"],
    text: { h6: "家长聊天群", p: "高能预警！家长群炫富模式开启" }
  },
  {
    img: "//qidian.gtimg.com/acts/2019/57928942/images/boy/2.png",
    tags: ["幻想世界", "长篇追读"],
    text: { h6: "师父，大师兄让妖怪抓走了！", p: "西天取经只为报销路费" }
  }
];

class Card extends Component {
  constructor(props) {
    super(props);
    this.dragStart = this.dragStart.bind(this);
    this.cardTransform = this.cardTransform.bind(this);
    this.indexTransform = this.indexTransform.bind(this);
    this.prev = this.prev.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      // 当前展示的图片index
      currentIndex: 0,
      // 记录偏移量
      displacement: {
        x: 0,
        y: 0
      },
      start: { x: 0, y: 0 },
      // 位置信息
      position: {
        //start: { x: 0, y: 0 },
        end: { x: 0, y: 0 },
        direction: 1, // 滑动方向，左是-1，右是1
        swipping: false // 是否在拖动交换过程中
      },
      // 记录每一个丢出去的方向
      directionArr: [],
      // 显示图片的堆叠数量
      visible: 3,
      // 视口宽度
      winWidth: 0,
      //  滑动阈值
      slideWidth: 70,
      // 超过阈值时的自动偏移量
      offsetWidth: 120
      // temporaryData: {
      //   lastPosWidth: "",
      //   lastPosHeight: ""
      // }
    };
  }
  state = {};

  // 初始化每张卡片的样式
  cardTransform(index) {
    let style = {};
    //卡片自动位移距离（飞出屏幕多远）
    let offset = 0;
    if (this.state.directionArr[index] === 1) {
      offset = 800;
    } else if (this.state.directionArr[index] === -1) {
      offset = -800;
    }

    style["zIndex"] = this.state.currentIndex - index + this.state.visible;
    style["transform"] = `translate3d(0,0,${(this.state.currentIndex - index) *
      60}px)`;

    //让藏在后面的卡片缩小样式堆叠在一起并透明不显示。一旦飞走一张，下一张卡片会自动过渡动画往前推进
    if (index - this.state.currentIndex < 0) {
      style["opacity"] = 0;
      style["transform"] = `translate3d(${this.state.position.end.x +
        offset}px,${this.state.position.end.y}px,${(this.state.currentIndex -
        index) *
        60}px) rotate(${this.state.position.direction * -65}deg)`;
    }

    //非手势滑动状态才添加过渡动画;
    if (!this.state.position.swipping) {
      style["transitionTimingFunction"] = "ease";
      style["transitionDuration"] = 300 + "ms";
    }
    return style;
  }
  //第一张卡片的样式
  indexTransform(index) {
    let style = {};
    if (index === this.state.currentIndex) {
      style["transform"] = `translate3d(${this.state.displacement.x}px,${
        this.state.displacement.y
      }px,${(this.state.currentIndex - index) * 60}px) rotate(${(this.state
        .displacement.x /
        this.state.winWidth) *
        -65}deg)`;
      //style["transformOrigin"] = "left top";
    }

    //非手势滑动状态才添加过渡动画;
    if (!this.state.position.swipping) {
      style["transitionTimingFunction"] = "ease";
      style["transitionDuration"] = 300 + "ms";
    }

    return style;
  }

  componentDidMount() {
    this.setState({
      winWidth: window.innerWidth
    });
  }
  offsetRatio() {
    //let width = this.refs.card.offsetWidth;
  }
  dragStart() {}
  touchStart(e) {
    // if (this.state.position.swipping) {
    //   return;
    // }
    if (e.touches.length > 1) {
      this.setState({
        position: {
          ...this.state.position,
          swipping: false
        }
      });
      return;
    } else {
      this.setState({
        start: {
          ...this.state.start,
          x: e.targetTouches[0].clientX,
          y: e.targetTouches[0].clientY
        }
      });
      // this.setState({
      //   position: {
      //     ...this.state.position,
      //     start: {
      //       ...this.state.position.start,
      //       x: e.targetTouches[0].clientX,
      //       y: e.targetTouches[0].clientY
      //     },
      //     end: {
      //       ...this.state.position.end,
      //       x: e.targetTouches[0].clientX,
      //       y: e.targetTouches[0].clientY
      //     }
      //   }
      // });
    }
    this.setState({
      position: {
        ...this.state.position,
        swipping: true
      }
    });
  }
  touchMove(e) {
    if (this.state.position.swipping) {
      this.setState({
        position: {
          ...this.state.position,
          end: {
            ...this.state.position.end,
            x: e.targetTouches[0].clientX,
            y: e.targetTouches[0].clientY
          }
        }
      });
    }
    this.setState({
      displacement: {
        ...this.state.displacement,
        x: this.state.position.end.x - this.state.start.x,
        y: this.state.position.end.y - this.state.start.y
      }
    });
  }
  touchEnd(e) {
    //滑动结束，触发判断
    this.setState({
      position: {
        ...this.state.position,
        // end: {
        //   ...this.state.position.end,
        //   x: this.state.displacement.x,
        //   y: this.state.displacement.y
        // },
        swipping: false
      }
    });

    //判断滑动距离超过设定值时，自动飞出
    if (this.state.displacement.x > this.state.slideWidth) {
      //往右
      this.moveNext(1);
    } else if (this.state.displacement.x < -this.state.slideWidth) {
      //往左
      this.moveNext(-1);
    }
    this.setState({
      displacement: {
        ...this.state.displacement,
        x: 0,
        y: 0
      }
    });
    // this.$nextTick(() => {
    //   this.displacement.x = 0;
    //   this.displacement.y = 0;
    //   this.isDrag = false;
    // });
  }
  moveNext(direction) {
    //alert("next");
    let data = this.state.position;
    data.direction = direction;
    this.setState({
      position: data
    });
    console.log(this.state.position.direction);

    //防止在最后倒数几张时操时出错
    try {
      this.dataArr[this.state.currentIndex + 3].display = true;
    } catch (e) {}

    //防止在第一张时操作出错;
    if (this.state.currentIndex > 0) {
      try {
        this.dataArr[this.state.currentIndex - 1].display = false;
      } catch (e) {}
    }

    let currentIndex = this.state.currentIndex;
    currentIndex++;
    let offsetWidth = this.state.offsetWidth;
    let endX = this.state.position.end["x"] - offsetWidth;
    let endXLeft = this.state.position.end["x"] + offsetWidth;
    let endY = this.state.position.end["y"] + offsetWidth / 2;
    //每次让下一张卡片往前推进，反之 -- 就是返回上一张

    //alert(endX);

    this.setState({
      currentIndex: currentIndex
    });
    if (direction > 0) {
      console.log("right");
      //alert("test");
      this.setState({
        position: {
          ...this.state.position,
          end: {
            ...this.state.position.end,
            x: endX,
            y: endY
          }
        }
      });
    } else {
      console.log("left");
      //alert("else");
      this.setState({
        position: {
          ...this.state.position,
          end: {
            ...this.state.position.end,
            x: endXLeft,
            y: endY
          }
        }
      });
      console.log(this.state.position.end.x);
      //alert(this.state.position.end.x);
    }
    // this.state.currentIndex++;
    // !direction
    //   ? (this.state.position.end["x"] -= this.state.offsetWidth)
    //   : (this.state.position.end["x"] += this.state.offsetWidth);
    // this.state.position.end["y"] += this.state.offsetWidth / 2;
  }
  // nextTick() {
  //   this.setState({
  //     temporaryData: {
  //       ...this.state.temporaryData,
  //       lastPosWidth: this.state.displacement.x,
  //       lastPosHeight: this.state.displacement.y
  //     }
  //   });
  // }
  prev() {
    this.moveNext(1);
  }
  next() {
    // let width = this.refs.card.offsetWidth;
    // this.setState({
    //   displacement: {
    //     ...this.state.displacement,
    //     x: width,
    //     y: 0
    //   }
    // });
    // this.setState({
    //   position: {
    //     ...this.state.position,
    //     direction: -1
    //   }
    // });
    // console.log(this.refs.card.offsetWidth);
    // console.log("next");
    this.moveNext(-1);
  }
  render() {
    return (
      <div id="app">
        <div
          className="activity-container font-family"
          style={{ backgroundColor: "rgba(255, 255, 255, 0)" }}
        >
          <a className="banner" href="#/card">
            <img
              src="//qidian.qpic.cn/qidian_common/349573/4e27870b2b8fcb410d841e4e860c90e2/0"
              alt="在APP打开"
              className="banner-img"
            />
            <div className="banner-close" href="#/card">
              <img
                alt=""
                src="//bossaudioandcomic-1252317822.image.myqcloud.com/activity/document/78c37f2185a6e5fef924bec7af264980.svg"
              />
            </div>
            {/* <a className="banner-close" href="#/card">
              <img
                alt=""
                src="//bossaudioandcomic-1252317822.image.myqcloud.com/activity/document/78c37f2185a6e5fef924bec7af264980.svg"
              />
            </a> */}
          </a>
          <div className="activity">
            <div id="component-10954">
              <div>
                <div id="app" className="warp noah-chat-57928942 color-1">
                  <div className="mask" />
                  <div className="card_container" ref="card">
                    {cardInfos.map((cardInfo, index) => {
                      const tags = cardInfo.tags.map((tag, index) => {
                        return <span key={index}>{tag}</span>;
                      });
                      return (
                        <div
                          key={index}
                          onDragStart={this.dragStart}
                          onTouchStart={e => {
                            this.touchStart(e);
                          }}
                          onTouchMove={e => {
                            this.touchMove(e);
                          }}
                          onTouchEnd={e => {
                            this.touchEnd(e);
                          }}
                          // onMouseDown={e => {
                          //   this.touchStart(e);
                          // }}
                          // onMouseUp={e => {
                          //   this.touchMove(e);
                          // }}
                          // onMouseMove={e => {
                          //   this.touchEnd(e);
                          // }}
                          className={[
                            "card ",
                            this.state.currentIndex -
                              index +
                              this.state.visible <=
                            0
                              ? null
                              : "draw"
                          ].join("")}
                          style={{
                            ...this.cardTransform(index),
                            ...this.indexTransform(index)
                          }}
                          // style={this.indexTransform(index)}
                          // style={{
                          //   zIndex: 3,
                          //   transform:
                          //     "translate3d(0px, 0px, 0px) rotate(0deg)",
                          //   transitionTimingFunction: "ease",
                          //   transitionDuration: 300
                          // }}
                        >
                          <div className="img-box">
                            <img alt="" src={cardInfo.img} />
                            <div className="alpha-box" />
                            <div className="tag show">{tags}</div>
                          </div>
                          <div className="info">
                            <div className="text">
                              <h6>{cardInfo.text.h6}</h6>
                              <p>{cardInfo.text.p}</p>
                            </div>
                            <em className="read-btn">
                              <i>阅读</i>
                              <span />
                            </em>
                          </div>
                        </div>
                      );
                    })}

                    {/* <div
                      onDragStart={this.dragStart}
                      className="card draw"
                      style={{
                        zIndex: 2,
                        transform: "translate3d(0px, 0px, -60px)",
                        transitionTimingFunction: "ease",
                        transitionDuration: 300
                      }}
                    >
                      <div className="img-box">
                        <img
                          alt=""
                          src="//qidian.gtimg.com/acts/2019/57928942/images/boy/2.png"
                        />
                        <div className="alpha-box" />
                        <div className="tag">
                          <span>幻想世界</span>
                          <span>长篇追读</span>
                        </div>
                      </div>{" "}
                      <div className="info">
                        <div className="text">
                          <h6>笼中对追凶游戏</h6>
                          <p>这场游戏里，你必须找到凶手，或者成为凶手……</p>
                        </div>
                        <em className="read-btn">
                          <i>阅读</i>
                          <span />
                        </em>
                      </div>
                    </div>
                    <div
                      onDragStart={this.dragStart}
                      className="card draw"
                      style={{
                        zIndex: 1,
                        transform: "translate3d(0px, 0px, -120px)",
                        transitionTimingFunction: "ease",
                        transitionDuration: 300
                      }}
                    >
                      <div className="img-box">
                        <img alt="" src="" />
                        <div className="alpha-box" />
                        <div className="tag">
                          <span>幻想世界</span>
                          <span>长篇追读</span>
                        </div>
                      </div>
                      <div className="info">
                        <div className="text">
                          <h6>话不可以乱说</h6>
                          <p>小心，这个群里说的任何话都会成真哦...</p>
                        </div>
                        <em className="read-btn">
                          <i>阅读</i>
                          <span />
                        </em>
                      </div>
                    </div> */}
                  </div>
                  <div className="auto-height" />
                  <div className="btn_group">
                    <a
                      href="#/card"
                      className="back disabled"
                      onClick={this.prev}
                    >
                      {" "}
                    </a>
                    <a href="#/card" className="add-fav" onClick={this.next}>
                      {" "}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Card;
