import { createStore } from "redux";

const store = (() => {
  const initialState = {
    // Component specific state
    sx: 70,
    sy: 200,
    c1x: 200,
    c1y: 330,
    c2x: 200,
    c2y: 70,
    ex: 330,
    ey: 200,

    // Canvas and draggable specific state
    canvasWidth: 500,
    canvasHeight: 500,
    width: 400,
    height: 400,
    zoom: 1
  };

  const reducer = (state = initialState, action) => {
    function overrideCoord(x, y) {
      x = Math.min(x, state.width);
      y = Math.min(y, state.height);
      x = Math.max(x, 0);
      y = Math.max(y, 0);
      x = Math.round(x);
      y = Math.round(y);
      return { x, y };
    }
    switch (action.type) {
      case "S": {
        const { x, y } = overrideCoord(action.x, action.y);
        return Object.assign({}, state, {
          sx: x,
          sy: y
        });
      }
      case "C1": {
        const { x, y } = overrideCoord(action.x, action.y);
        return Object.assign({}, state, {
          c1x: x,
          c1y: y
        });
      }
      case "C2": {
        const { x, y } = overrideCoord(action.x, action.y);
        return Object.assign({}, state, {
          c2x: x,
          c2y: y
        });
      }
      case "E": {
        const { x, y } = overrideCoord(action.x, action.y);
        return Object.assign({}, state, {
          ex: x,
          ey: y
        });
      }
      case "RESIZE":
        const minSize = Math.min(action.width, action.height);
        if (minSize <= 500) {
          return Object.assign({}, state, {
            canvasWidth: minSize,
            canvasHeight: minSize,
            zoom: state.width / minSize
          });
        } else {
          return state;
        }
      default:
        return state;
    }
  };

  return createStore(reducer);
})();

export default store;
