import React, { Component } from "react";
import "./flexboxFroggy.css";

class FlexboxFroggy extends Component {
  state = {};
  componentWillMount() {}
  render() {
    return (
      <div
        style={{
          display: "flex",
          minWidth: 600,
          margin: 0,
          backgroundColor: "#43A047",
          color: "rgba(255, 255, 255, 0.7)",
          fontFamily: "PT Sans"
        }}
      >
        <section id="sidebar">
          <div className="header">
            <div>
              <h1 className="header-title">Flexbox Froggy</h1>
            </div>
            <div id="level-counter">
              <span className="arrow left">◀</span>
              <span id="level-indicator">
                <span id="labelLevel" className="translate">
                  Level
                </span>
                <span className="current">1</span>
                <span id="labelOf" className="translate">
                  of
                </span>
                <span className="total">1</span>
                <span className="caret">▾</span>
              </span>
              <span className="arrow right">▶</span>
              <div id="levelsWrapper" className="tooltip">
                <div id="levels" />
                <div id="labelReset" className="translate">
                  Reset
                </div>
              </div>
            </div>
          </div>
          <p id="instructions" />
          <div id="editor">
            <div id="css">
              <div className="line-numbers">
                1<br />2<br />3<br />4<br />5<br />6<br />7<br />8<br />9<br />
                10
              </div>
              <pre id="before" />
              <textarea id="code" autoFocus />
              <pre id="after" />
            </div>
            <button id="next" className="translate">
              Next
            </button>
          </div>
          <div id="share">
            <a
              className="next gridgarden"
              href="https://codepip.com/games/grid-garden/"
            >
              <div>
                <button>Grid Garden</button>
              </div>
            </a>
            <a
              className="next treehouse"
              href="http://treehouse.7eer.net/c/371033/228915/3944?subId1=flexboxfroggy"
            >
              <div>
                <button>Treehouse</button>
              </div>
            </a>
            <p className="social">
              <span id="tweet">
                <a
                  href="https://twitter.com/share"
                  className="twitter-share-button"
                >
                  Tweet
                </a>
                <a
                  href="https://twitter.com/playcodepip"
                  className="twitter-follow-button"
                >
                  Follow @playcodepip
                </a>
              </span>
              <span className="fb-like" />
            </p>
          </div>
          <div className="native-ad">
            <a href="#native_link#">
              <span className="sponsor">Sponsor</span> #native_company# —
              #native_desc#
            </a>
          </div>

          <div className="credits">
            <span id="labelFooter" className="translate">
              Flexbox Froggy is created by
            </span>
            <a href="https://codepip.com">Codepip</a> •
            <a href="https://github.com/thomaspark/flexboxfroggy/">GitHub</a> •
            <a href="https://twitter.com/playcodepip">Twitter</a> •
            <span id="settings">
              <span id="labelSettings" className="toggle translate">
                Settings
              </span>
              <span className="tooltip">
                <section>
                  <h4 id="labelLanguage" className="translate">
                    Language
                  </h4>
                  <select id="language">
                    <option value="en">English</option>
                    <option value="es">Español</option>
                    <option value="fr">Français</option>
                    <option value="de">Deutsch</option>
                    <option value="nl">Nederlands</option>
                    <option value="pt-br">Português</option>
                    <option value="it">Italiano</option>
                    <option value="sv">Svenska</option>
                    <option value="pl">Polski</option>
                    <option value="cs">Česky</option>
                    <option value="hu">Magyar</option>
                    <option value="ro">Română</option>
                    <option value="bg">Български</option>
                    <option value="lv">Latviešu</option>
                    <option value="lt">Lietuvių</option>
                    <option value="uk">Українська</option>
                    <option value="ru">Русский</option>
                    <option value="sr">Српски</option>
                    <option value="mk">Македонски</option>
                    <option value="el">Ελληνικά</option>
                    <option value="tr">Türkçe</option>
                    <option value="fa">فارسی</option>
                    <option value="hi">हिंदी</option>
                    <option value="ta">தமிழ்</option>
                    <option value="ml">മലയാളം</option>
                    <option value="zh-cn">简体中文</option>
                    <option value="zh-tw">繁體中文</option>
                    <option value="ja">日本語</option>
                    <option value="ko">한국어</option>
                    <option value="vi">Tiếng Việt</option>
                    <option value="eo">Esperanto</option>
                  </select>
                </section>
                <section>
                  <h4 id="labelDifficulty" className="translate">
                    Difficulty
                  </h4>
                  <form id="difficulty">
                    <div>
                      <input
                        type="radio"
                        name="difficulty"
                        id="difficultyEasy"
                        value="easy"
                      />
                      <label id="labelDifficultyEasy" className="translate">
                        Beginner
                      </label>
                    </div>
                    <div>
                      <input
                        type="radio"
                        name="difficulty"
                        id="difficultyMedium"
                        value="medium"
                      />
                      <label id="labelDifficultyMedium" className="translate">
                        Intermediate - No Directions
                      </label>
                    </div>
                    <div>
                      <input
                        type="radio"
                        name="difficulty"
                        id="difficultyHard"
                        value="hard"
                      />
                      <label id="labelDifficultyHard" className="translate">
                        Expert - No Directions &amp; Random Levels
                      </label>
                    </div>
                  </form>
                </section>
                <section>
                  <h4 id="labelColorblind" className="translate">
                    Colorblind Mode
                  </h4>
                  <form id="colorblind">
                    <div>
                      <div>
                        <input
                          type="radio"
                          name="colorblind"
                          id="colorblindOff"
                          value="false"
                        />
                        <label id="labelColorblindOff" className="translate">
                          Off
                        </label>
                      </div>
                      <div>
                        <input
                          type="radio"
                          name="colorblind"
                          id="colorblindOn"
                          value="true"
                        />
                        <label id="labelColorblindOn" className="translate">
                          On
                        </label>
                      </div>
                    </div>
                  </form>
                </section>
              </span>
            </span>
          </div>
          <div className="credits">
            <span id="gridGarden" className="translate">
              Want to learn CSS grid? Play
            </span>{" "}
            <a href="https://codepip.com/games/grid-garden/">Grid Garden</a>.
          </div>
        </section>
        <section id="view">
          <div id="board">
            <div id="pond" />
            <div id="background" />
          </div>
        </section>
      </div>
    );
  }
}

export default FlexboxFroggy;
