import React, { Component } from "react";
import Zmage from "react-zmage";
import "./content.css";

class Content extends Component {
  state = {};
  render() {
    return (
      <div className="content">
        <div
          className="wrapper"
          style={{
            marginTop: 14,
            backgroundColor: "#FFE3E1"
          }}
        >
          <div className="title">
            <div className="title-main" style={{ color: "#E81706" }}>
              新品推荐
            </div>
            <div className="count-down" />
          </div>
          <div className="rows">
            <div className="row-item">
              <a href="#/">
                <div className="image-wrapper">
                  <Zmage
                    className="row-3 row-item-image row-item-image-3"
                    src="//img.alicdn.com/tfs/TB1RBxNngaTBuNjSszfXXXgfpXa-800-800.png_200x200.jpg_.webp"
                  />
                  <div className="item-icon">限时抢</div>
                </div>
                <div className="row-3 row-item-title">新乐敦滴眼液</div>
                <div className="row-3 row-item-tag">
                  <div className="tag">第二件半价</div>
                </div>
                <div className="row-item-price">
                  <div className="price-mark">¥</div>
                  <div className="price">49</div>
                  <div className="origin-price">¥76</div>
                </div>
              </a>
            </div>
            <div className="row-item">
              <a href="#/">
                <div className="image-wrapper">
                  <Zmage
                    className="row-3 row-item-image row-item-image-3"
                    src="//img.alicdn.com/imgextra/i1/2928278102/O1CN01iRF0AO29ilRfOdiSz_!!2928278102.png_200x200.jpg_.webp"
                  />
                  <div className="item-icon">限时抢</div>
                </div>
                <div className="row-3 row-item-title">达霏欣生发搽剂</div>
                <div className="row-3 row-item-tag">
                  <div className="tag">前1000件半价</div>
                </div>
                <div className="row-item-price">
                  <div className="price-mark">¥</div>
                  <div className="price">118</div>
                  <div className="origin-price">¥128</div>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Content;
