import React, { Component } from "react";
import { connect } from "react-redux";
import { Observable } from "rxjs";

class Canvas extends Component {
  componentDidMount() {
    let resize = Observable.fromEvent(window, "resize").map(() => ({
      width: window.innerWidth,
      height: window.innerHeight
    }));

    resize.forEach(({ width, height }) => {
      this.props.resize(width, height);
    });
  }
  state = {};
  render() {
    return (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        version="1.1"
        width={this.props.canvasWidth}
        height={this.props.canvasHeight}
        viewBox={`0 0 ${this.props.width} ${this.props.height}`}
      >
        {this.props.children}
      </svg>
    );
  }
}

const canvasMapStateToProps = (state, ownProps) => {
  return {
    canvasWidth: state.canvasWidth,
    canvasHeight: state.canvasHeight,
    width: state.width,
    height: state.height
  };
};

const canvasMapDispatchToProps = (dispatch, ownProps) => {
  return {
    resize: (width, height) => {
      dispatch({
        type: "RESIZE",
        width,
        height
      });
    }
  };
};

export default connect(
  canvasMapStateToProps,
  canvasMapDispatchToProps
)(Canvas);
